<!--helpCenter-->
<template>
  <article class="user-article">
    <div id="helpCenter"></div>
  </article>
</template>
<script>
  import "@/static/js/jquery.min.js"

export default {
  name: 'helpCenter',
  data () {
    return {
    }
  },
  watch:{
    $route(d){
      this.loadPages(d)
    }
  },
  methods:{
    loadPages(d){
      let p=d.query.p;
      let v=d.query.v;
      if(p&&v){
        this.$nextTick(()=>{
          $("#helpCenter").load(`/static/static-resources/html/helpCenter/${p}.html?v=${v}`)
        })
      }

    }

  },

   mounted(){
     this.loadPages(this.$route)
   },

}
</script>


<style scoped>
  .order-box{border: 1px solid #ddd; padding: 20px}
  .note-info { padding: 10px; border: 1px solid #ddd; border-left: none; border-right: none}
  .order-sum { color: #FF4E00; font-weight: 700; margin-right: 30px; }

</style>





